@import '../../../style/variables.scss';

.single-comic {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 293px 550px auto;
    column-gap: 50px;
    align-items: start;
    &__img {
        width: 293px;
        height: 450px;
        @media (max-width: 767px) {
            height: 400px;
        }
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
    }
    &__descr {
        font-size: 18px;
        line-height: 24px;
        margin-top: 25px;
    }
    &__price {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        color: $main-color;
        margin-top: 25px;
    }
    &__back {
        justify-self: end;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        &:hover {
            color: $main-color;
        }
        @media (max-width: 1023px) {
            display: none;
        }
    }
    &__hidden {
        display: none;
        @media (max-width: 1023px) {
            display: block;
            margin-top: 40px;
        }
    }
    @media (max-width: 1200px) {
        grid-template-columns: 293px 475px auto;
    }
    @media (max-width: 1023px) {
        grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 767px) {
        grid-template-columns: 1fr;
        justify-items: center;
        text-align: center;
        row-gap: 50px;
    }
}

